import React, { ReactElement } from 'react';
import './App.css';
import Login from './tournament/Login';
import StatusBar from './StatusBar';
import Tournament from './Tournament';
import { POST, cookies, setErrorCallback } from './utils/requests';
//import { isElectron, isDev, isProd } from './utils/electron';

import Menu from './tournament/Menu';
import Alert from './tournament/Alert';

export default class App extends React.Component<{}, {
	sessionID: string
	tournamentID: string

	mode: 'login' | 'menu' | 'tournament' | 'grid'

	alertBox: {
		message: string
		title?: string
		confirm?: boolean
		resolve?: () => void
		reject?: () => void
	}
}> {

	public state = {
		sessionID: '',
		tournamentID: '',
		mode: 'login' as 'login' | 'menu' | 'tournament' | 'grid',

		alertBox: { message: '', error: true, confirm: false }
	}

	public closeAlertBox = (): void => this.setState({ alertBox: { message: '' }});
	public triggerConfirm = (message: string, resolve: () => void, reject: () => void) => this.setState({ alertBox: {message, title: 'CONFIRM ACTION', confirm: true, resolve, reject }});
	public triggerError = (message: string) => this.setState({ alertBox: {message, title: 'ERROR', confirm: false }});

	public setTournamentID = async (tournamentID: string): Promise<void> => {
		await cookies.set('tournamentID', tournamentID, { path: '/' });
		this.setState({
			tournamentID,
			mode: tournamentID ? 'tournament' : 'menu'
		});
	}

	public hasLoggedIn = async (sessionID: string): Promise<void> => {
		await cookies.set('sessionID', sessionID, { path: '/' });
		this.setState({ sessionID, mode: 'menu' });
	}

	public closeTournament = async (): Promise<void> => {
		await cookies.set('tournamentID', '', { path: '/' });
		this.setState({ tournamentID: '', mode: 'menu' });
	}

	private endSession = async (): Promise<void> => {
		try {
			await cookies.set('sessionID', '', { path: '/' });
			await POST({ url: '/endSession' });
		} catch (e) {
			console.error(e);
		} finally {
			this.setState({ sessionID: '', mode: 'login' });
		}
	}

	async componentDidMount(): Promise<void> {
		setErrorCallback((errorMessage: string) => this.triggerError(errorMessage));
		let sessionID = await cookies.get('sessionID');
		if (sessionID) this.setState({ sessionID, mode: 'menu' });
	}

	componentWillUnmount() {
		setErrorCallback(() => {});
	}


	render(): ReactElement {
		return (
			<>
				<StatusBar/>
				<div className='stage' style={{
					backgroundImage: `url(${process.env.PUBLIC_URL}/images/leaves-texture.png)`
				}}>
					{(() => {
						switch (this.state.mode) {
						case 'login': {
							return <Login login={this.hasLoggedIn}/>;
						}
						case 'menu': {
							return <Menu
								sessionID={this.state.sessionID}
								endSession={this.endSession}
								setTournamentID={this.setTournamentID}
								triggerConfirm={this.triggerConfirm}
							/>;
						}
						case 'tournament': {
							return <Tournament
								id={this.state.tournamentID}
								closeTournament={this.closeTournament}
								triggerError={this.triggerError}
							/>;
						}
						default: {
							console.error('invalid mode');
							this.setState({ mode: 'login' });
							return null;
						}
						}
					})()}
					<Alert
						{...this.state.alertBox}
						close={this.closeAlertBox}
					/>
				</div>
			</>
		);
	}
}