import React, { ReactElement, useEffect, useCallback, useState } from 'react';
import url from 'url';

import appStyles from '../css/app.module.css';
import profileStyles from '../css/profile.module.css';
import styles from '../css/menu.module.css';

import { NumberDictionary, StringDictionary, NavData } from '../interfaces';
import { Crown } from './Login';
import { GET, POST, cookies, server } from '../utils/requests';
import { AxiosError } from 'axios';
import { Circle } from '../components/Vectors';
import { calculateHypotenuse } from '../utils/prototype';
import { defaultUserSettings } from '../assets/settings';
import { EditableField } from '../components/';

import { IoMdArrowRoundBack, IoIosSettings, IoMdClose, IoIosCloseCircle } from 'react-icons/io';
import { AiOutlinePlus } from 'react-icons/ai';
import { TiThMenu } from 'react-icons/ti';
import { MdRefresh } from 'react-icons/md';
import { FaEarlybirds, FaPlusCircle } from 'react-icons/fa';

export default class Menu extends React.Component<{
	sessionID: string
	endSession: () => void
	setTournamentID: (id: string) => void
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
}, {
	tournamentDict: StringDictionary,
	loading: boolean
	loadingOverride: boolean
	register: boolean
	errorMessage: string
	lines: ReactElement[]
	linesMounted: boolean
	linesAdjusted: boolean
	selected: string
	hovered: string
}> {

	public state = {
		tournamentDict: {} as StringDictionary,
		loading: true,
		loadingOverride: false,
		register: false,
		errorMessage: '',
		lines: [] as ReactElement[],
		linesMounted: false,
		linesAdjusted: false,
		selected: '',
		hovered: ''
	}

	public setTournamentID = (tournamentID: string): Promise<void> => {
		return this.validateTournament(tournamentID)
			.then(() => {
				cookies.set('tournamentID', tournamentID, { path: '/' }).catch(console.error);
				this.props.setTournamentID(tournamentID);
			})
			.catch(() => {
				cookies.remove('tournamentID').catch(console.error);
				this.props.setTournamentID('');
			});
	}

	public validateTournament(tournamentID: string): Promise<void> {
		if (!tournamentID) return Promise.reject();
		if (!Object.values(this.state.tournamentDict).length) {
			return this.updateTournamentDict()
				.then((tournamentDict: StringDictionary) => {
					if (tournamentDict[tournamentID] === undefined) throw new Error();
					return;
				});
		} else return tournamentID in this.state.tournamentDict ? Promise.resolve() : Promise.reject();
	}

	public updateTournamentDict = (): Promise<StringDictionary> => {
		return GET({ url: '/tournaments' })
			.then((tournamentDict: StringDictionary) => {
				this.setState({ tournamentDict });
				return tournamentDict;
			})
			.catch((e: Error) => {
				console.error(e);
				this.props.endSession();
				return {} as StringDictionary;
			});
	}

	public createNewTournament = async (): Promise<void> => {
		try {
			let id = await POST({ url: '/initialise' });
			if (!id) throw new Error();
			this.props.setTournamentID(id);
			await this.updateTournamentDict();
		} catch (error) {
			let e = error as AxiosError;
			if (!e.response || e.response.status === 401) this.props.endSession();
		}
	}

	public removeID = async (id?: string): Promise<void> => {
		if (!id) {
			await POST({ url: '/closeAuthorization/all' });
			this.setState({ tournamentDict: {} });
		} else {
			let tournamentDict = this.state.tournamentDict;
			delete tournamentDict[id];
			await POST({ url: '/closeAuthorization/' + id });
			this.setState({ tournamentDict });			
		}
	}

	private getElementCentre(elem: HTMLElement): {x: number, y: number} {
		var rect = elem.getBoundingClientRect();
		return {
			x: (rect.left + rect.right) / 2,
			y: (rect.top + rect.bottom) / 2 - 30 //on account of parent being 30px from top
		};
	}

	renderLineBetween(id: string, start: {x: number, y: number}, end: {x: number, y: number}, width: number): ReactElement {
		let diff = {x: Math.abs(end.x - start.x), y: Math.abs(end.y - start.y)};
		let length = calculateHypotenuse(end.x - start.x, end.y - start.y);

		let sign = Math.sign((end.x - start.x) * (end.y - start.y));
		let angle = Math.atan(diff.y / diff.x) * 180 / Math.PI;

		return <div key={['line', end.x, end.y].join('.')} className={styles.line} style={{
			width: length + 'px',
			height: width + 'px',
			backgroundColor: 'white',
			position: 'absolute',
			left: !this.state.linesMounted ? start.x - (diff.x / 2) : ((start.x + end.x) / 2) - (diff.x / 2),
			top: !this.state.linesMounted ? start.y : ((start.y + end.y) / 2),
			transform: `rotate(${angle * sign}deg) scaleX(${this.state.linesMounted ? 1 : 0}) scaleY(1)`,
			opacity: !this.state.linesMounted ? 0 : 1,
			transition: 'transform 3s, left 3s, top 3s, opacity 3s'
		}}/>;
	}

	private logoRef = React.createRef() as React.RefObject<HTMLDivElement>
	private drawLines = () => {
		if (!this.logoRef.current) return;
		let startCoords = this.getElementCentre(this.logoRef.current);
		let circles = Array.from(document.getElementsByClassName(styles.dot)) as HTMLElement[];
		let lines = [] as ReactElement[];
		for (let c of circles) {
			if (c.style.opacity === '0') continue;
			lines.push(this.renderLineBetween(c.id, startCoords, this.getElementCentre(c), 2));
		}
		this.setState({ lines, linesMounted: true });
		setTimeout(() => this.setState({ linesAdjusted: true }), 3000);
	}

	private handleNavData(keys: string[], side: 'left' | 'right'): ReactElement[] {
		let navData = this.navData;
		let data = keys.reduce((acc: NavData, curr: string) => {
			if (!(curr in navData)) return acc;
			acc[curr] = navData[curr];
			return acc;
		}, {});
		return Object.entries(data).reduce((acc, [id, meta], i) => {
			let arr = [
				<div className={[styles.dotDescription, this.state.selected === id ? styles.descriptionSelected : ''].join(' ')} key={['navData', side, 'description', i].join('.')}>
					{meta.description}
				</div>,
				<Circle
					key={['navData', side, 'dot', i].join('.')}
					radius='50px'
					className={[styles.dot, !this.state.linesAdjusted ? styles.dotUnloaded : ''].join(' ')}
					id={id}
					content={meta.content}
					onClick={async (e) => {	
						if (this.state.selected === id) this.setState({ selected: '' });
						else {				
							await meta.onClick(e);
							if (!meta.noSet) this.setState({ selected: id });
						}
					}}
					onMouseEnter={() => this.setState({ hovered: 'back' })}
					onMouseLeave={() => this.setState({ hovered: '' })}
					style={meta.style}
				/>
			];
			if (side === 'right') arr = arr.reverse();
			acc.push(<div className={[styles.dotContainer, this.state.linesAdjusted ? styles.dotContainerLoaded : ''].join(' ')} key={['NavData', side, 'div', i].join('i')}>
				{arr}
			</div>);
			return acc;
		}, [] as ReactElement[]);
	}

	private get navData(): NavData {
		return {
			create: {
				onClick: this.createNewTournament,
				content: <AiOutlinePlus />,
				description: 'Create new Swiss tournament'
			},
			roundRobin: {
				onClick: () => {},
				content: <FaEarlybirds />,
				description: 'Create new Round-Robin tournament'
			},
			settings: {
				onClick: () => {},
				content: <IoIosSettings />,
				description: 'User settings'
			},
			load: {
				onClick: this.updateTournamentDict,
				content: <TiThMenu />,
				description: 'Load previous tournaments'
			},
			back: {
				onClick: this.props.endSession,
				content: <IoMdArrowRoundBack />,
				description: 'Exit to login'
			},
			close: {
				onClick: () => this.props.triggerConfirm('Delete all saved tournaments?', this.removeID, () => {}),
				noSet: true,
				content: <IoMdClose />,
				description: 'Close all tournaments',
				style: {					
					border: '8px solid darkred'
				}
			},
			refresh: {
				onClick: this.updateTournamentDict,
				noSet: true,
				content: <MdRefresh />,
				description: 'Refresh tournament list',
				style: {					
					border: '8px solid darkblue'
				}
			},
		};
	}

	componentDidUpdate(prevProps: {}, prevState: {selected: string}) {
		if (this.state.selected !== prevState.selected) this.drawLines();
	}
    
    private loadingTimeout!: NodeJS.Timeout
	async componentDidMount() {
		let tournamentID = await cookies.get('tournamentID');
		if (tournamentID) this.props.setTournamentID(tournamentID);
		else this.updateTournamentDict();
		window.addEventListener('resize', this.drawLines);
		this.drawLines();
		setImmediate(this.drawLines);
		//setTimeout(this.drawLines, 2000);
		this.loadingTimeout = setTimeout(() => this.setState({ loading: false }), 3000);
	}

	componentWillUnmount() {		
        window.addEventListener('resize', this.drawLines);
        clearTimeout(this.loadingTimeout);
	}

	render(): ReactElement {
		return (
			<div className={appStyles.canvas}>
				{this.state.lines}
				<div className={[appStyles.column, styles.leftColumn].join(' ')}>
					{(() => {
						switch (this.state.selected) {
						case 'load':
							return this.handleNavData(['refresh', 'close', 'load'], 'left');
						case 'settings':
							return this.handleNavData(['create', 'settings', 'load'], 'left');
						default:
							return this.handleNavData(['create', 'settings', 'load'], 'left');
						}
					})()}
				</div>
				<div ref={this.logoRef} className={[appStyles.column, styles.column].join(' ')}>
					<Crown setLoading={(loadingOverride: boolean) => this.setState({ loadingOverride })} loading={this.state.loading || this.state.loadingOverride} classNames={{
						logo: styles.logo
					}}/>
				</div>
				<div className={[appStyles.column, styles.rightColumn].join(' ')}>
					{(() => {
						switch (this.state.selected) {
						case 'load':
							return <Listing
								setTournamentID={this.props.setTournamentID}
								createNewTournament={this.createNewTournament}
								removeID={this.removeID}
								updateTournamentSettings={this.updateTournamentDict}
								tournamentDict={this.state.tournamentDict}
								closeListing={() => this.setState({ selected: '' })}
								triggerConfirm={this.props.triggerConfirm}
							/>;
						case 'settings':
							return <UserSettings
								updateTournamentSettings={() => Promise.resolve()}
								updateSetting={() => Promise.resolve()}
								settings={{}}
								closeListing={() => this.setState({ selected: '' })}
							/>;
						default:
							return this.handleNavData(['back'], 'right');
						}
					})()}
				</div>
			</div>
		);
	}


}

interface ListingProps {
	setTournamentID: (id: string) => void
	createNewTournament: () => Promise<void>
	removeID: (id: string) => void
	closeListing: () => void
	updateTournamentSettings: () => void
	tournamentDict: StringDictionary
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
}

export function Listing(props: ListingProps) {

    const [height, setHeight] = useState(0);

	const generateListing = useCallback((tournamentDict: StringDictionary) => {
		let titles = Object.keys(tournamentDict).reduce((acc: NumberDictionary, curr: string) => {
			let title = props.tournamentDict[curr];
			if (!title) return acc;
			if (!acc[title]) acc[title] = 0;
			acc[title]++;
			return acc;
		}, {});
		let buttons = Object.entries(tournamentDict).map(([tournamentID, name], i: number) => {
			let displayName = titles[name] === undefined ? tournamentID : titles[name] === 1 ? name : name + '\n' + tournamentID;
			return (
				<div className={[profileStyles.row, styles.row].join(' ')} key={['row', i].join('.')}>
					<div
						className={[styles.clickButton, 'fieldBox'].join(' ')}						
						onClick={() => props.setTournamentID(tournamentID)}
					>
						{displayName}
					</div>
					<div className={[styles.clickButton, styles.close, 'fieldBox'].join(' ')} onClick={() => {
						props.triggerConfirm('Delete tournament \'' + displayName + '\'?\nPlease note this action cannot be undone.', () => props.removeID(tournamentID), () => {});
					}}>
						<IoIosCloseCircle />
					</div>
				</div>
			);
		}).filter(elem => elem);
		return buttons;
	}, [props.tournamentDict, props.setTournamentID, props.triggerConfirm, props.removeID]);

    const listing = React.useRef() as React.RefObject<HTMLDivElement>
    
    useEffect(() => {
        let x = setTimeout(() => {
            if (!listing.current) return;
            setHeight(listing.current.getBoundingClientRect().height);
        }, 1000);
        return () => clearTimeout(x);
    }, [listing, props.tournamentDict, generateListing, setHeight]);

    return (
        <div className={styles.sidebar}>
            <div className={[profileStyles.profile, styles.profile].join(' ')}>				
                <div className='topBanner'>
                    Load Tournament
                </div>
                <div className={styles.listWrapper} style={{height}}>
                    <div ref={listing} className={styles.listing}>
                        {generateListing(props.tournamentDict)}
                    </div>
                </div>
            </div>
            <div className={['bottomBanner', profileStyles.bottomBanner].join(' ')}>
                <div onClick={props.createNewTournament} className='button'>
                    <FaPlusCircle />
                </div>
                <div onClick={props.closeListing} className='button'>
                    <IoMdArrowRoundBack />
                </div>
            </div>
        </div>
    );
}

export class UserSettings extends React.Component<{
	updateTournamentSettings: () => Promise<void>
	updateSetting: (k: string, v: any) => Promise<void>
	settings: any
	closeListing: () => void
}> {


	private renderSettings(): ReactElement[] {
		return Object.entries(defaultUserSettings).map(([k, def], i): ReactElement => {
			let v = this.props.settings[k] || def.defaultValue;
			let locked = false;
			if (def.locked) locked = true;
			let content = def.convert ? def.convert(v) : v as string;
			let displayContent = typeof content !== 'undefined' ? content.toString() : '\u200b';
			return (
				<div className={profileStyles.row} key={['row', i].join('.')}>
					<div className='fieldBox'>{def.name + ':'}</div>
					{def.type === 'number' ? <EditableField
						handleSubmit={this.props.updateSetting}
						content={displayContent}
						url={url.resolve(server, ['updateUserSetting'].join('/'))}
						inputType='number'
						onSubmitSuccess={(e) => {
							this.props.updateTournamentSettings();
							return e;
						}}
						additionalStyles={{
							editableField: profileStyles.editableField,
							input: profileStyles.input
						}}
						name={k}
						additionalInputProps={{}}
						show={{
							submit: true,
							increment: true
						}}
					/> :
						<div
							className={['fieldBox', !locked ? profileStyles.editable : profileStyles.locked].join(' ')}
							onClick={locked ? () => {} : () => this.props.updateSetting(k, v)}
						>
							{displayContent}
						</div>}
				</div>
			);
		});
	}

	render(): ReactElement {
		return (
			<div className={styles.sidebar}>
				<div className={[profileStyles.profile, styles.profile].join(' ')}>	
                    <div className='topBanner'>
                        Settings
                    </div>
                    {this.renderSettings()}
                    <div style={{
                        width: '100%',
                        height: '100%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center'
                    }}>
                        <div style={{
                            transform: 'rotate(-30deg)'
                        }}>
                            This feature is in beta
                        </div>
                    </div>
                </div>
                <div className={['bottomBanner', profileStyles.bottomBanner].join(' ')}>
                    <div onClick={this.props.closeListing} className='button'>
                        <IoMdArrowRoundBack />
                    </div>
                </div>
            </div>
		);
	}
}