import React, { ReactElement } from 'react';
import styles from '../css/alert.module.css';

export default class Alert extends React.Component<{
	message: string
	title?: string
	confirm?: boolean
	resolve?: () => void
	reject?: () => void
	close: () => void
}> {
	
	render(): ReactElement {
		if (!this.props.message) return null;
		let message = typeof this.props.message === 'string' ? this.props.message : JSON.stringify(this.props.message, null, 4);
		if (message.length < 10) return null;
		return (
			<div className={styles.alertBox}>
				{this.props.title ? <div>{this.props.title}</div> : null}
				<div className={[styles.textBox].join(' ')}>
					{message}
				</div>
				<div className={styles.response}>
					{
						!this.props.confirm ?
							<div className={[styles.NO, 'button'].join(' ')} onClick={this.props.close}>
								OK
							</div> :
							<>
								<div className={[styles.NO, 'button'].join(' ')} onClick={() => {
									this.props.reject();
									this.props.close();
								}}>
									✗
								</div>
								<div className={[styles.YES, 'button'].join(' ')} onClick={() => {
									this.props.resolve();
									this.props.close();
								}}>
									✔
								</div>
							</>
					}
				</div>
			</div>
		);
	}

}