import React, { useEffect, useState, useRef, ReactElement, useCallback } from 'react';
import { useMouseDown } from '.';
import { FORM } from '../utils/requests';
import styles from '../css/components.module.css';
import { AiOutlineClose, AiOutlineDown, AiOutlineUp, AiOutlineCheck } from 'react-icons/ai';

interface EditableFieldProps {
	content: string
	defaultEditContent?: string
	defaultContent?: string
	inputType?: 'button' | 'checkbox' | 'color' | 'date' | 'datetime-local' | 'email' | 'file' | 'hidden' | 'image' | 'month' | 'number' | 'password' | 'radio' | 'range' | 'reset' | 'search' | 'submit' | 'tel' | 'text' | 'time' | 'url' | 'week',

	url: string, //url.resolve(server, ['tournament', props.id, 'editTournamentName'].join('/'))
	handleSubmit: (k: string, v: any) => Promise<void>
    onSubmitSuccess: (e: React.FormEvent<HTMLFormElement>) => React.FormEvent<HTMLFormElement>
    handleClear?: () => Promise<void>
	validateInput?: (form: HTMLFormElement) => Promise<void>
	additionalStyles: {[key: string]: string}
	additionalInputProps: {[key: string]: any}
	name?: string
	show?: {
		submit?: boolean
		infinity?: boolean
		increment?: boolean
        any?: boolean
        clear?: boolean
	}
}

function roundUpDate(date: Date): Date {
	let interDate = new Date(date.setUTCHours(date.getUTCHours() + 1));
	let nextDate: Date;
	if (interDate.getUTCMinutes() < 30) nextDate = new Date(interDate.setUTCMinutes(30));
	else nextDate = new Date(new Date(interDate.setUTCMinutes(0)).setUTCHours(interDate.getUTCHours() + 1));
	return nextDate;
}

function toTimeDate(date: Date): string {
	if (isNaN(date.valueOf())) return '';
	return date.toLocaleString('en-GB').slice(0, -3);
	//return date.toTimeString().slice(0, 8) + ' ' + date.toDateString().slice(0, 10);
}

function toDatetimeLocal(date: Date) {
	if (isNaN(date.valueOf())) return '';
	let tzoffset = date.getTimezoneOffset() * 60000; //offset in milliseconds
	let localDate = new Date(date.valueOf() - tzoffset); 
	return localDate.toISOString().slice(0, 16);
}

export default function EditableField (props: EditableFieldProps): ReactElement {
	
    const [content, setContent] = useState(props.content);
    const [editContent, setEditContent] = useState(false);
    const [mustFocus, focus] = useState(false);
    const inputField = useRef(null as HTMLInputElement);
    const divArea = useRef(null as HTMLDivElement);	

    const decrement = (e: React.MouseEvent<HTMLButtonElement>): void => {
        e.preventDefault();
        let button = e.target as HTMLButtonElement;
        if (button.constructor.name !== 'HTMLButtonElement') return;
        let form = button.parentNode as HTMLFormElement;
        let input = form[props.name || 'name'] as HTMLInputElement;
        input.stepDown();
    }
    
    const increment = (e: React.MouseEvent<HTMLButtonElement>): void => {
        e.preventDefault();
        let button = e.target as HTMLButtonElement;
        if (button.constructor.name !== 'HTMLButtonElement') return;
        let form = button.parentNode as HTMLFormElement;
        let input = form[props.name || 'name'] as HTMLInputElement;
        input.stepUp();
    }

	const handleClear = useCallback((e: React.MouseEvent<HTMLButtonElement>): Promise<void> => {
        e.preventDefault();
        if (!props.handleClear) return;
		return props.handleClear()
			.then(() => {
                setEditContent(false);
                setContent('');
            });
	}, [props.handleClear]);

	const handleAny = (e: React.MouseEvent<HTMLButtonElement>): Promise<void> => {
        e.preventDefault();
		return props.handleSubmit(props.name || 'name', 0)
			.then(() => {
                setEditContent(false);
                setContent('0');
            });
	}

	const handleInfinity = (e: React.MouseEvent<HTMLButtonElement>): Promise<void> => {
        e.preventDefault();
		return props.handleSubmit(props.name || 'name', 'Infinity')
			.then(() => {
                setEditContent(false);
                setContent('∞');
            });
	}

	const handleSubmit = (e: React.FormEvent<HTMLFormElement>): Promise<void> => {
        e.preventDefault();
		return FORM(e)
			.then(async () => {
				let form = e.target as HTMLFormElement;
				if (props.validateInput) await props.validateInput(form);						
			})
			.then(() => {
                setEditContent(false);
				return e;
			})
			.then(props.onSubmitSuccess)
			.then((e) => {
                let form = e.target as HTMLFormElement;
                while (form.constructor.name !== 'HTMLFormElement' && form.parentElement) form = form.parentElement as HTMLFormElement;
                if (form.constructor.name !== 'HTMLFormElement') throw new Error('Couldn\'t find valid form');
				let input = form.firstChild as HTMLInputElement;
                let content = input.value;
                setContent(content);
			});
    }
    
	const exitEditing = (e: React.KeyboardEvent<HTMLFormElement>): React.KeyboardEvent<HTMLFormElement> => {
        switch (e.keyCode) {
            case 27: {
                setEditContent(false);
                return e;
            }
            case 13: {
                handleSubmit(e);
                return e;
            }
            default: {
                return e;
            }
        }
	}

	const generateForm = (name: string): JSX.Element => {
		let defaultValue = name || props.defaultEditContent;
		if (props.inputType === 'datetime-local') defaultValue = toDatetimeLocal(roundUpDate(new Date(defaultValue)));
		else if (props.show?.any && defaultValue === '0') defaultValue = '';
		return (
			<form
				className={styles.form}
				target='_self'
				onKeyDown={exitEditing}
				onSubmit={handleSubmit}
				method='POST'
				action={props.url}
			>
				<input
					type={props.inputType}
					className={[styles.input, props.additionalStyles.input].join(' ')}
					name={props.name || 'name'}
					defaultValue={defaultValue || '\u200b'}
					required
					ref={inputField}
					{...props.additionalInputProps}
				/>
				<button
					className={[props.show?.submit && !props.show?.infinity && !props.show?.any && !props.show?.clear ? '' : styles.submitHidden, styles.cancel].join(' ')}
					onClick={() => setEditContent(false)}
				>
                    <AiOutlineClose />
                </button>
				<button
					className={[!props.show?.clear ? styles.submitHidden : '', styles.cancel].join(' ')}
					onClick={handleClear}
				>
                    <AiOutlineClose />
                </button>
				<button
					className={[!props.show?.any ? styles.submitHidden : '', styles.any].join(' ')}
					onClick={handleAny}
				>Any</button>
				<button
					className={[!props.show?.infinity ? styles.submitHidden : '', styles.infinity].join(' ')}
					onClick={handleInfinity}
				>∞</button>
				<button
					type='button'
					value='▼'
					className={[!props.show?.increment ? styles.submitHidden : '', styles.increment].join(' ')}
					onClick={decrement}
				>
                    <AiOutlineDown />
                </button>
				<button
					className={[!props.show?.increment ? styles.submitHidden : '', styles.increment].join(' ')}
					onClick={increment}
                    >
                    <AiOutlineUp />
                </button>
				<button
					type='submit'
					value='✔'
                    className={[!props.show?.submit ? styles.submitHidden : '', styles.submit].join(' ')}
                >
                    <AiOutlineCheck />
                </button>
			</form>
		);
    }
    
    useEffect(() => {
        setContent(props.content);
    }, [props.content]);
    useEffect(() => {
        if (!mustFocus) return;
        let form = inputField.current;
        if (form) form.select();
    }, [mustFocus]);
    useMouseDown((e) => {
		if (divArea.current && divArea.current.contains(e.target as Node)) return;
        setEditContent(false);
    });

	const myID = Math.random().toString(16).slice(2, 9);
	
	let displayContent = content;
	if (displayContent && props.inputType === 'datetime-local') displayContent = toTimeDate(new Date(content));
	else if (props.show?.any && displayContent === '0') displayContent = 'Any';

    return (
        <div
            ref={divArea}
            className={[styles.container, props.additionalStyles.container].join(' ')}
            id={myID}
        >
            {editContent ?
                generateForm(content) :
                <div
                    className={[styles.editableField, props.additionalStyles.editableField || ''].join(' ')}
                    onClick={() => {
                        setEditContent(true);
                        focus(true);
                    }}
                >
                    {editContent ? 
                        generateForm(content) :
                        displayContent || props.defaultContent || '\u200b'
                    }
                </div>
            }
        </div>
    );

}