import React, { useState } from 'react';
import { win } from './utils/electron';
import { FaWindowMaximize, FaWindowRestore, FaWindowMinimize, FaWindowClose } from 'react-icons/fa';

import styles from './css/statusbar.module.css';

export default function StatusBar() {
    
    const [name, setName] = useState('Tournament by Oxford Chess Development');

	const maximised = win.isMaximized();
    const minimise = win.minimize();
    const close = win.close();

	const toggleMaximisation = () => {
		if (maximised) {
			win.restore();
		} else {
			win.maximize();
		}
	}

    return (
        <div className={styles.statusBar}>
            <div className={styles.title}>
                {name}
            </div>
            <div className={styles.menu}>
                <div className={styles.favicon}>
                    <img
                        alt='favicon'
                        draggable={false}
                        src={process.env.PUBLIC_URL + '/logo192.png'}
                    />
                </div>
            </div>
            <div className={styles.exec}>
                <div onClick={minimise}>
                    <FaWindowMinimize />
                </div> 
                <div onClick={toggleMaximisation} >
                    {maximised ? 
                        <FaWindowRestore /> :
                        <FaWindowMaximize />
                    }
                </div> 
                <div onClick={close} className={styles.close}>
                    <FaWindowClose />
                </div>
            </div>
        </div>
    );
}