export function Crown(props: {
	loading: boolean,
	setLoading: (loading: boolean) => void
	classNames?: StringDictionary
}) {

	return (
		<div
			className={[appStyles.logo, props.classNames?.logo].join(' ')}
		>
			{!props.loading ? 
				<img className={appStyles.staticLogo} src={process.env.PUBLIC_URL + '/images/favicon.png'} alt='swiss-logo.png'/> :
				<>
					<img className={appStyles.staticLogo} src={process.env.PUBLIC_URL + '/images/dubrovny.png'} alt='swiss-logo.png'/>
					<img className={appStyles.crownOutline} src={process.env.PUBLIC_URL + '/images/crown-outline.png'} alt='outline-gif'/>
					<img className={appStyles.loadingGif} src={process.env.PUBLIC_URL + '/images/loading-crown.gif'} alt='loading-gif'/>
				</>
			} {/*
			<div>
				<hr className={appStyles.loadingLine}/>
				<hr className={appStyles.loadingLine}/>
				<hr className={appStyles.loadingLine}/>
			</div>*/}
		</div>
	);
}

export default class Login extends React.Component<{
	login: (sessionID: string) => void
}, {
	loading: boolean
	register: boolean
	errorMessage: string
}> {

	public state = {
		loading: false,
		register: false,
		errorMessage: ''
	}

	private handleSubmit = (e: React.FormEvent<HTMLFormElement>): void => {
		this.setState({loading: true});
		FORM(e)
			.then((sessionID: string) => this.props.login(sessionID))
			.catch((e) => {
				if (e && e.response && e.response.data) this.setState({
					errorMessage: e.response.data
				});
				console.error(e);
			})
			.finally(() => {
				this.setState({loading: false});
			});
	}

	private get loginForm(): ReactElement {
		return (
			<form
				className={styles.form}
				method='POST'
				onSubmit={this.handleSubmit}
				action={url.resolve(server, 'login')}
			>
				<input type='text' name='username' required placeholder='username' className={['input', styles.input].join(' ')}></input>
				<input type='password' name='password' required placeholder='password' className={['input', styles.input].join(' ')}></input>
				<div className={styles.forgottenContainer}>
					<div className={['input', styles.forgotten].join(' ')}>username reminder</div>
					<div className={['input', styles.forgotten].join(' ')}>password reset</div>
				</div>
				<input type='submit' value='LOGIN' className={['input', styles.submit].join(' ')}></input>
			</form>
		);
	}

	private get registerForm(): ReactElement {
		return (
			<form
				className={styles.form}
				method='POST'
				onSubmit={this.handleSubmit}
				action={url.resolve(server, 'register')}
			>
				<input type='text' name='username' required placeholder='username' className={['input', styles.input].join(' ')}></input>
				<input type='password' name='password' required placeholder='password' minLength={8} className={['input', styles.input].join(' ')}></input>
				<input type='email' name='email' required placeholder='email' className={['input', styles.input].join(' ')} pattern={regexes.email.source}></input>
				<input type='submit' value='REGISTER' className={['input', styles.submit].join(' ')}></input>
			</form>
		);
	}

	private get loginSection(): ReactElement {
		return (
			<div className={[styles.loginSection, styles.initialiseButton].join(' ')}>
				<div className={styles.loginBackground}>
					<div className={[styles.flip, 'button'].join(' ')} onClick={() => {
						this.setState({
							loading: true,
							register: false,
							errorMessage: ''
						});
						setTimeout(() => this.setState({ loading: false }), 500);
					}}>
						<div>LOGIN</div>
						<div>
                            <IoIosArrowBack />
                        </div>
					</div>
					<div></div>
					<div className={[styles.flip, 'button'].join(' ')} onClick={() => {
						this.setState({
							loading: true,
							register: true,
							errorMessage: ''
						});
						setTimeout(() => this.setState({ loading: false }), 500);
					}}>
						<div>REGISTER</div>
						<div>
                            <IoIosArrowForward />
                        </div>
					</div>
					Background
				</div>
				<div className={[styles.foreground, this.state.register ? styles.registerForeground : styles.loginForeground].join(' ')}>
					{
						this.state.register ? this.registerForm : this.loginForm
					}
				</div>
			</div>
		);
	}

	render(): ReactElement {
		return (
			<div className={appStyles.canvas}>
				<div className={appStyles.column} />
				<div className={[appStyles.column, styles.column].join(' ')}>
					<Crown setLoading={(loading: boolean) => this.setState({ loading })} loading={this.state.loading} />
					{this.loginSection}
					<div className={styles.messageBar}>
						{this.state.errorMessage || '\u200b'}
					</div>
					{/*<div className={['button', styles.initialiseButton].join(' ')}>
						{this.state.register ? <>
							<div className={styles.registerForm}>{this.registerForm}</div>
							<div className={styles.flip} onClick={() => this.setState({ register: false })}>{'LOGIN\n🡄'}</div>
						</> : <>
							<div className={styles.flip} onClick={() => this.setState({ register: true })}>{'REGISTER\n🡆'}</div>
							<div className={styles.loginForm}>{this.loginForm}</div>
						</>}
					</div>*/}
				</div>
				<div className={appStyles.column} />
			</div>
		);
	}


}