import React, { ReactElement } from 'react';
import Grid from '../components/Grid';
import { TournamentStatus, Player, TournamentSettings } from '../interfaces';

import styles from '../css/crosstable.module.css';
import { MdExitToApp } from 'react-icons/md';

export default class CrossTable extends React.Component<{
	setDisplayPlayer: (id: string) => void
	show: () => void
	status: TournamentStatus
	players: Player[]
	settings: TournamentSettings
}, {
	sortValue: string,
	byRounds: boolean
}> {

	public state = {
		sortValue: 'score',
		byRounds: true
	}

	get bodyByRobin(): JSX.Element[][] {
		return [[]];
	}

	get totalRounds():number {
		let totalRounds: number;
		if (this.props.status.round > this.props.settings.totalRounds) totalRounds = this.props.status.round;
		else if (this.props.settings.totalRounds === 'Infinity') totalRounds = this.props.status.round;
		else totalRounds = this.props.settings.totalRounds;
		return totalRounds;
	}

	header(): {[key: string]: string} {
		let rounds = [] as string[];
		
		for (let i = 1; i <= this.totalRounds; i++) {
			rounds.push((i).toString());
		}
		return {
			id: '',
			name: 'Name',
			rating: 'Rating',
			...Object.entries(rounds).reduce((acc, [k, v]) => { acc['score.' + k] = v; return acc;}, {} as {[key: string]: string}),
			prettyScore: 'Score',
			winPercentage: 'TB',
			performanceRating: 'Perf',
			ratingDifference: 'P. +/-',
			expectedScore: 'Expected',
			expectedDifference: 'E. +/-'
		}; //{[key: string]: {name: string, sortable: boolean, headerClass: string, bodyClass: string}}
	}

	public handleHeaderClick = (sortValue: string): void => {
		if (sortValue.startsWith('score.')) return;
		if (sortValue === 'expectedScore') return;
		if (sortValue === 'prettyScore') sortValue = 'score';
		this.setState({ sortValue });
	}

	private calcLength = 6;

	classNames(): string[] {
		let rounds = [] as string[];
		for (let i = 1; i <= this.totalRounds; i++) {
			rounds.push(styles.resultsCell);
		}
		return [
			'',
			'',
			...rounds,
			'',
			'',
			'',
			'',
			'',
			''
		];
	}

	bodyByRounds(state: { sortValue: string }): JSX.Element[][] {
		return this.props.players
			.map((p) => {
				let scores = [] as string[];
				for (let j = 1; j <= this.totalRounds; j++) {
					let minPlayer = p.histories[j];
					if (!j) continue;
					else if (!minPlayer) scores.push('');
					else if (typeof minPlayer.match !== 'number') scores.push('*');
					else if (minPlayer.match === 0.5) scores.push('½');
					else if (minPlayer.oppRating === undefined) scores.push(minPlayer.match.toString() + ('b'));
					else scores.push(minPlayer.match.toString());
				}
				let ratingDifference = p.performanceRating - p.rating;
				let expectedDifference = p.score - p.expectedScore;
				return {
					id: p.id,
					name: this.props.settings.competitors === 'individual' ? [p.firstName, p.lastName].join(' ') : p.firstName,
					rating: p.rating,
					...Object.entries(scores).reduce((acc, [k, v]) => { acc['score.' + k] = v; return acc;}, {} as {[key: string]: string}),
					score: p.score,
					prettyScore: p.score.toFixed(1) + ' / ' + p.played,
					winPercentage: p.played === 0 ? '-' : (p.score * 100 / p.played).toFixed(2),
					performanceRating: p.performanceRating ? p.performanceRating.toFixed() : '',
					ratingDifference: p.performanceRating ? ratingDifference > 0 ? '+' + ratingDifference.toString() : ratingDifference.toString() : '',
					expectedScore: p.expectedScore.toFixed(2) + ' / ' + p.played,
					expectedDifference: expectedDifference > 0 ? '+' + expectedDifference.toFixed(2) : expectedDifference.toFixed(2)
				};
			})
			.sort((a, b) => {
				if (!(state.sortValue in a)) return 0;
				let x: any, y: any;
				if (state.sortValue !== 'name') {
					x = Number((a as {[key: string]: string | number})[state.sortValue]);
					y = Number((b as {[key: string]: string | number})[state.sortValue]);
				} else {
					x = a[state.sortValue];
					y = b[state.sortValue];
				}
				if (x !== y) return y - x;
				return (b.rating as number) - (a.rating as number);
			})
			.map((p, i) => {
				let rows = [
					['Ranking', i.toString()],
					...Object.entries(p)
				];
				return rows.filter(([k]) => {
					if (k === 'id') return false;
					if (k === 'score') return false;
					return true;
				}).map(([key, value], j) => {
					let isPlayer = j === 1;
					let isScore = j > 2 && j < rows.length - this.calcLength;
					return (						
						<div
							id={isPlayer ? p.id : [value, j].join('.')}
							key={[value, j].join('.')}
							className={[
								styles.cell,
								isScore ? styles.resultsCell : '',
								isPlayer ? styles.playerBox : '',
								j === 0 ? styles.firstColumn : ''
							].join(' ')}
							onClick={!isPlayer ? () => {} : (e: React.MouseEvent<HTMLDivElement>) => this.props.setDisplayPlayer((e.target as HTMLDivElement).id)}
						>
							{value || '\u200b'}
						</div>
					);
				});
			});		
	}

	render(): ReactElement {
		//styles.cell.height = `calc(100vh - ${body.length + 1})`;
		
		let body = this.state.byRounds ? this.bodyByRounds(this.state) : this.bodyByRobin;
		let title = `Tournament '${this.props.status.name || this.props.status.id}' results after ${this.props.status.round} rounds`;
		let subtitle = '';
		return (
			<>				
				<div className={styles.titles}>
					<div>{title}</div>
					<div>{subtitle}</div>
					<div className={['button', styles.closeButton].join(' ')} onClick={this.props.show}>
                        <MdExitToApp />
                    </div>
				</div>
				<Grid
					header={this.header()}
					body={body}
					styles={styles}
					bodyClassNames={this.classNames()}
					style={{
						gridColumn: '1 / span 2'
					}}
					handleHeaderClick={this.handleHeaderClick}
				/>
			</>
		);	
	}
}