import React, { ReactElement } from 'react';

interface GridProps {
	header: {[key: string]: string | undefined}
	body: JSX.Element[][]
	styles: any
	bodyClassNames: string[]
	style: any
	handleHeaderClick: (key: string) => void
}

export default function Grid(props: GridProps): ReactElement {

	const headers = props.header;
	const body = props.body;

	return (
		<div className={props.styles.crossTable} style={props.style}>
			<table className={props.styles.table}>
				<thead>
					<tr>
						{Object.entries(headers).map(([key, header], i) => {
							return <th scope='column' key={['column', i].join('.')} className={props.styles.th}>
								{header ? <div
									className={[props.styles.cell, props.styles.headerCell].join(' ')}
									onClick={() => props.handleHeaderClick(key)}
								>
									{header}
								</div> : ''}
							</th>;
						})}
					</tr>
				</thead>
				<tbody>						
					{body.map((rowData, i) => {
						let first = rowData.shift();
						let rest = rowData;
						return (
							<tr key={'row.' + i} className={props.styles.tr}>
								<th scope='row'>
									{first}
								</th>
								{rest.map((cell, j) => {
									let UID = ['cell', i, j].join('.');
									return (
										<td id={UID} key={UID} className={props.styles.td}>
											{cell}
										</td>
									);
								})}												
							</tr>
						);
					})}
				</tbody>
			</table>
		</div>
	);
	
}